import {
    Inject,
    Injectable,
} from '@angular/core';

import { DOCUMENT } from '@angular/common';

function _window(): any {
    return window;
}

@Injectable()
export class WindowRef {

    constructor( @Inject(DOCUMENT) private document: any) {
    }

    get nativeWindow(): any {
        return _window();
    }

    get nativeDocument(): any {
        return this.document;
    }

}



