    getDateForWeek(week: number, dd: Date): string {

        const daysToSubstract = (this.pregnancyInWeeks - week) * 7;
        const date = new Date(
            dd.getTime() - (daysToSubstract * 24 * 60 * 60 * 1000));

        const month = this.monthNames[date.getMonth()];
        return date.getDate().toString() + ' ' + month + ' ' + date.getFullYear().toString();
    }

    toList(items: string[]): string {

        let output = '<ul>';
        for (const item of items) {
            output += `<li>${item}</li>`;
        }
        output += '</ul>';
        return output;
    }
