import { BrowserModule } from '@angular/platform-browser';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';

import { FormsModule } from '@angular/forms';
import { Router } from '@angular/router';

import {
  ButtonModule,
  InputTextModule,
  CalendarModule,
  CheckboxModule,
  DropdownModule,
  RadioButtonModule,
  DataTableModule,
  SharedModule,
  MessagesModule,
  MessageModule,
} from 'primeng/primeng';

// Routing
import { RoutingModule } from './app.routing';

// Components
import { AppComponent } from './app.component';
import { HomeComponent } from './components/home/home.component';
import { HeaderComponent } from './components/header/header.component';
import { CalendarComponent } from './components/calendar/calendar.component';

// Services
import {
  PersonService,
  CalendarService,
  LoggingService,
  MenuService,
  WindowRef,
} from './services';

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    HeaderComponent,
    CalendarComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    RoutingModule,
    ButtonModule,
    InputTextModule,
    CalendarModule,
    CheckboxModule,
    DropdownModule,
    RadioButtonModule,
    DataTableModule,
    SharedModule,
    MessagesModule,
    MessageModule,
    FormsModule,
  ],
  providers: [
    PersonService,
    CalendarService,
    LoggingService,
    MenuService,
    WindowRef,
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



