import {
    Component,
    Input,
} from '@angular/core';

import {
    Scheme,
    Item,
} from '../../models';

import {
    CalendarService,
    LoggingService,
    MenuAction,
    MenuService,
    WindowRef,
} from '../../services';

@Component({
    selector: 'app-calendar',
    templateUrl: './calendar.component.html',
    styleUrls: ['./calendar.component.scss'],
})

export class CalendarComponent {

    currentPatient: string;
    currentDeliveryDate: Date;
    currentDeliveryDateString: string;

    currentScheme: Scheme;
    currentSchemeName: string;

    hasCalendar: boolean;

    calendar: Item[];
    cols: any[];

    @Input() set patient(model: string) {
        this.currentPatient = model;
        this.update();
    }

    @Input() set deliveryDate(model: Date) {
        this.currentDeliveryDate = model;
        if (model) {
            const month = this.calenderService.monthNames[model.getMonth()];
            this.currentDeliveryDateString =
                model.getDate().toString() + ' ' + month + ' ' + model.getFullYear().toString();
        } else {
            // clear
            this.currentDeliveryDateString = '';
        }
        this.update();
    }

    @Input() set scheme(model: Scheme) {
        this.currentScheme = model;
        this.currentSchemeName = Scheme[this.currentScheme];
        this.update();
    }

    constructor(
        protected logger: LoggingService,
        protected calenderService: CalendarService,
        protected menuService: MenuService,
        protected window: WindowRef,
    ) {
        this.menuService.menuAction$.subscribe(
            data => {
                const action: MenuAction = data;
                switch (action) {
                    case MenuAction.Print:
                        this.onPrint();
                        break;
                }
            }
        );
    }

    update() {
        if (this.currentScheme && this.currentDeliveryDate) {

            this.hasCalendar = true;
            this.calendar = this.calenderService.getCalendar(this.currentScheme, this.currentDeliveryDate);

            this.cols = [
                { field: 'appointment', header: 'Afspraak' },
                { field: 'date', header: 'Voorkeursdatum' },
                { field: 'who', header: 'Bij wie' },
                { field: 'what', header: 'Waarvoor' }
            ];
        } else {

            this.hasCalendar = false;
            this.calendar = this.calenderService.getCalendar(Scheme.NONE, null);
        }
    }

    onPrint() {
        this.logger.info('printing');

        const nativeDocument = this.window.nativeDocument;
        const nativeWindow = this.window.nativeWindow;

        const printContent = nativeDocument.getElementById('printable').innerHTML;
        const popupWin = nativeWindow.open('', '_blank', 'top=0,left=0,height=100%,width=auto');

        popupWin.document.open();
        popupWin.document.write(`
            <html>
                <head>
                    <style>
                    body {
                        font-family: Roboto,Trebuchet MS,Arial,Helvetica,sans-serif;
                    }
                    #calendar table {
                        border: 1px solid black;
                    }
                    #calendar table thead tr:first-of-type>th:not(:last-child) {
                        border-right: 1px solid black;
                        border-bottom: 1px solid black;
                    }
                    #calendar table thead tr:first-of-type>th:last-child {
                        border-bottom: 1px solid black;
                    }
                    #calendar table tbody tr:not(:last-child)>td:not(:last-child) {
                        border-right: 1px solid black;
                        border-bottom: 1px solid black;
                    }
                    #calendar table tbody tr:not(:last-child)>td:last-child {
                        border-bottom: 1px solid black;
                    }
                    #calendar table tbody tr:last-child>td:not(:last-child) {
                        border-right: 1px solid black;
                    }
                    </style>
                </head>
                <body onload="window.print()">
                    ${printContent}
                </body>
            </html>`
        );
        popupWin.document.close();
    }
}



