import {
    Component,
} from '@angular/core';

import {
    MenuAction,
    MenuService,
    LoggingService,
} from '../../services';

@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss'],
})

export class HeaderComponent {

    constructor(
        protected menuService: MenuService,
        protected logger: LoggingService,
    ) {
    }

    onPrint() {
        this.logger.info('menu: print');

        this.menuService.publishAction(MenuAction.Print);
    }

    onClear() {
        this.logger.info('menu: clear');

        this.menuService.publishAction(MenuAction.Clear);
    }
}



