import {
    Routes,
    RouterModule,
} from '@angular/router';

// Components
import { HomeComponent } from './components/home/home.component';

// Routes
const appRoutes: Routes = [
    {
        path: '',
        component: HomeComponent,
    },
    {
        path: '**',
        redirectTo: '',
    },
];

export const RoutingModule = RouterModule.forRoot(appRoutes);



