import {
    Component,
    OnInit,
    Output,
    EventEmitter,
} from '@angular/core';

import {
    Person,
    Scheme
} from '../../models';

import {
    PersonService,
    CalendarService,
    LoggingService,
    MenuAction,
    MenuService,
} from '../../services';

@Component({
    selector: 'app-home',
    templateUrl: './home.component.html',
    styleUrls: ['./home.component.scss'],
})
export class HomeComponent implements OnInit {

    patient: string;
    deliveryDate: Date;

    cardinality: string;

    risk: string;
    isRiskVisible: boolean;

    followUp: string;
    isFollowUpVisible: boolean;

    firstPregnancy: string;
    isFirstPregnancyVisible: boolean;

    earlyBirth: string;
    isEarlyBirthVisible: boolean;

    monoChorial: string;
    isMonoChorialVisible; boolean;

    monoAmniotic: string;
    isMonoAmnioticVisible: boolean;

    scheme: Scheme;

    nl: any;

    constructor(
        protected personService: PersonService,
        protected calendarService: CalendarService,
        protected logger: LoggingService,
        protected menuService: MenuService,
    ) {
        this.menuService.menuAction$.subscribe(
            data => {
                const action: MenuAction = data;
                switch (action) {
                    case MenuAction.Clear:
                        this.onClear();
                        break;
                }
            }
        );
    }

    ngOnInit() {
        this.nl = {
            firstDayOfWeek: 0,
            dayNames: ['Zondag', 'Maandag', 'Dinsdag', 'Woensdag', 'Donderdag', 'Vrijdag', 'Zaterdag'],
            dayNamesShort: ['Zon', 'Maa', 'Din', 'Woe', 'Don', 'Vri', 'Zat'],
            dayNamesMin: ['Zo', 'Ma', 'Di', 'Wo', 'Do', 'Vr', 'Za'],
            monthNames: [
                'Januari', 'Februari', 'Maart', 'April', 'Mei', 'Juni', 'Juli', 'Augustus', 'September', 'Oktober', 'November', 'December'],
            monthNamesShort: ['Jan', 'Feb', 'Maa', 'Apr', 'Mei', 'Jun', 'Jul', 'Aug', 'Sep', 'Okt', 'Nov', 'Dec'],
            today: 'Vandaag',
            clear: 'Wissen'
        };
    }

    onDeliveryDateSelected(event: any) {
        this.logger.info('date: ' + this.deliveryDate);
    }

    onCardinalitySelected(event: any) {
        this.logger.info('cardinality: ' + this.cardinality);

        switch (this.cardinality) {
            case 'one':
                this.clearOne();
                this.isRiskVisible = true;
                break;
            case 'two':
                this.clearTwo();
                this.isMonoChorialVisible = true;
                break;
            case 'multiple':
                this.clearMultiple();
                this.setScheme(Scheme.MCDA);
                break;
        }
    }

    onRiskSelected(event: any) {
        this.logger.info('risk: ' + this.risk);

        switch (this.risk) {
            case 'low':
                this.clearScheme();

                this.earlyBirth = undefined;
                this.isEarlyBirthVisible = false;

                this.isFollowUpVisible = true;
                break;
            case 'medium':
                this.followUp = undefined;
                this.isFollowUpVisible = false;

                this.firstPregnancy = undefined;
                this.isFirstPregnancyVisible = false;

                this.earlyBirth = undefined;
                this.isEarlyBirthVisible = false;

                this.setScheme(Scheme.SCHEMA1);
                break;
            case 'high':
                this.clearScheme();

                this.followUp = undefined;
                this.isFollowUpVisible = false;

                this.firstPregnancy = undefined;
                this.isFirstPregnancyVisible = false;

                this.isEarlyBirthVisible = true;
                break;
        }
    }

    onFollowUpSelected(event: any) {
        this.logger.info('follow-up: ' + this.followUp);

        switch (this.followUp) {
            case 'gynonly':
                this.clearScheme();
                this.isFirstPregnancyVisible = true;
                break;
            case 'gynandgp':
                this.isFirstPregnancyVisible = false;
                this.firstPregnancy = undefined;

                this.setScheme(Scheme.SCHEMA3);
                break;
        }
    }

    onPregnancyCardinalitySelected(event: any) {
        this.logger.info('pregnancy cardinality: ' + this.firstPregnancy);

        switch (this.firstPregnancy) {
            case 'primipara':
                this.setScheme(Scheme.SCHEMA1);
                break;
            case 'multipara':
                this.setScheme(Scheme.SCHEMA2);
                break;
        }
    }

    onEarlyBirthSelected(event: any) {
        this.logger.info('early birth: ' + this.earlyBirth);

        switch (this.earlyBirth) {
            case 'earlybirthyes':
                this.setScheme(Scheme.SCHEMA5);
                break;
            case 'earlybirthno':
                this.setScheme(Scheme.SCHEMA4);
                break;
        }
    }

    onMonochorialSelected(event: any) {
        this.logger.info('monochorial: ' + this.monoChorial);

        switch (this.monoChorial) {
            case 'monochorialyes':
                this.clearScheme();
                this.isMonoAmnioticVisible = true;
                break;
            case 'monochorialno':
                this.monoAmniotic = undefined;
                this.isMonoAmnioticVisible = false;

                this.setScheme(Scheme.DCDA);
                break;
        }
    }

    onMonoamnioticSelected(event: any) {
        this.logger.info('monoamniotic: ' + this.monoAmniotic);

        switch (this.monoAmniotic) {
            case 'monoamnioticyes':
                this.setScheme(Scheme.MCMA);
                break;
            case 'monoamnioticno':
                this.setScheme(Scheme.MCDA);
                break;
        }
    }

    init() {
        this.cardinality = undefined;

        this.risk = undefined;
        this.isRiskVisible = false;

        this.followUp = undefined;
        this.isFollowUpVisible = false;

        this.firstPregnancy = undefined;
        this.isFirstPregnancyVisible = false;

        this.earlyBirth = undefined;
        this.isEarlyBirthVisible = false;

        this.monoChorial = undefined;
        this.isMonoChorialVisible = false;

        this.monoAmniotic = undefined;
        this.isMonoAmnioticVisible = false;

        this.deliveryDate = undefined;
        this.patient = '';

        this.clearScheme();
    }

    setScheme(scheme: Scheme) {
        this.logger.info('setting scheme: ' + scheme);

        this.scheme = scheme; // .emit(scheme);
    }

    clearScheme() {
        this.logger.info('clearing scheme');

        this.scheme = undefined; // .emit(undefined);
    }

    clearOne() {
        this.clearScheme();

        this.followUp = undefined;
        this.isFollowUpVisible = false;

        this.firstPregnancy = undefined;
        this.isFirstPregnancyVisible = false;

        this.earlyBirth = undefined;
        this.isEarlyBirthVisible = false;

        this.monoChorial = undefined;
        this.isMonoChorialVisible = false;

        this.monoAmniotic = undefined;
        this.isMonoAmnioticVisible = false;
    }

    clearTwo() {
        this.clearScheme();

        this.risk = undefined;
        this.isRiskVisible = false;

        this.followUp = undefined;
        this.isFollowUpVisible = false;

        this.firstPregnancy = undefined;
        this.isFirstPregnancyVisible = false;

        this.earlyBirth = undefined;
        this.isEarlyBirthVisible = false;

        this.monoAmniotic = undefined;
        this.isMonoAmnioticVisible = false;
    }

    clearMultiple() {
        this.risk = undefined;
        this.isRiskVisible = false;

        this.followUp = undefined;
        this.isFollowUpVisible = false;

        this.firstPregnancy = undefined;
        this.isFirstPregnancyVisible = false;

        this.earlyBirth = undefined;
        this.isEarlyBirthVisible = false;

        this.monoChorial = undefined;
        this.isMonoChorialVisible = false;

        this.monoAmniotic = undefined;
        this.isMonoAmnioticVisible = false;
    }

    onClear() {
        this.logger.info('clearing');

        this.init();
    }
}



